import React, { useEffect, useMemo, useState } from "react";
import Breadcrumb from "Components/Common/Breadcrumb";
import {
  Button,
  Col,
  Container,
  Form,
  FormFeedback,
  Input,
  Label,
  Modal,
  ModalHeader,
  Row,
} from "reactstrap";
import { column } from "Components/Common/type";
import TableContainer from "Components/Common/TableContainer";
import { DashboardUser, SetUsdpParam } from "./types";
import { useDispatch } from "react-redux";
import { createSelector } from "reselect";
import { useSelector } from "react-redux";
import { getUsers } from "slices/dashboard/thunk";
import Switch from "react-switch";
import { setUsdpInfo } from "helpers/backend_helper";
import * as Yup from "yup";
import { useFormik } from "formik";

const PosSymbol = () => {
  return (
    <div
      style={{
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        height: "100%",
        fontSize: 12,
        color: "#fff",
        paddingRight: 2,
      }}
    >
      <i className="fas fa-plus"></i>
    </div>
  );
};

const NegSymbol = () => {
  return (
    <div
      style={{
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        height: "100%",
        fontSize: 12,
        color: "#fff",
        paddingRight: 2,
      }}
    >
      <i className="fas fa-minus"></i>
    </div>
  );
};

const Dashboard = () => {
  const [modal_set_balance, setmodal_set_balance] = useState(false);
  const [switch1, setswitch1] = useState(true);
  const [userId, setUserId] = useState("");

  function tog_set_balance() {
    setmodal_set_balance(!modal_set_balance);
    removeBodyCss();
  }

  function removeBodyCss() {
    document.body.classList.add("no_padding");
  }

  const columns: column[] = useMemo(
    () => [
      {
        header: "",
        accessorKey: "number",
        enableColumnFilter: false,
        enableSorting: true,
      },
      {
        header: "",
        accessorKey: "nickname",
        enableColumnFilter: false,
        enableSorting: true,
      },
      {
        header: "()",
        accessorKey: "id",
        enableColumnFilter: false,
        enableSorting: true,
      },
      {
        header: "USDP",
        accessorKey: "usdp",
        enableColumnFilter: false,
        enableSorting: true,
      },
      {
        header: "",
        accessorKey: "adjust",
        enableColumnFilter: false,
        enableSorting: false,
        cell: (cellProps: any) => {
          return (
            <Button
              type="button"
              className="btn btn-soft-secondary btn-rounded"
              onClick={() => {
                setUserId(cellProps.row.original.id);
                setmodal_set_balance(true);
              }}
              data-toggle="modal"
            >
              <i className="far fa-edit"></i>
            </Button>
          );
        },
      },
      {
        header: "Token",
        accessorKey: "token",
        enableColumnFilter: false,
        enableSorting: true,
      },
      {
        header: " ",
        accessorKey: "attendance",
        enableColumnFilter: false,
        enableSorting: true,
      },
      {
        header: " ()",
        accessorKey: "prac",
        enableColumnFilter: false,
        enableSorting: true,
      },
      {
        header: " ()",
        accessorKey: "comp",
        enableColumnFilter: false,
        enableSorting: true,
      },
      {
        header: " ",
        accessorKey: "referral",
        enableColumnFilter: false,
        enableSorting: true,
      },
      {
        header: " ",
        accessorKey: "recom",
        enableColumnFilter: false,
        enableSorting: true,
      },
      {
        header: "",
        accessorKey: "signup",
        enableColumnFilter: false,
        enableSorting: true,
      },
      {
        header: " ",
        accessorKey: "last_access",
        enableColumnFilter: false,
        enableSorting: true,
      },
    ],
    []
  );

  interface selectState {
    dashboardUser: {
      //        
      dashboardUsers: DashboardUser[];
      loading: boolean;
    };
  }

  const selectProperties = createSelector(
    (state: selectState) => state.dashboardUser,
    (dashboard) => ({
      dashboardUsers: dashboard.dashboardUsers,
      loading: dashboard.loading,
    })
  );

  const dispatch = useDispatch<any>();
  const { dashboardUsers, loading } = useSelector(selectProperties);

  useEffect(() => {
    dispatch(getUsers());
  }, []);

  // Form validation
  const validation: any = useFormik({
    // enableReinitialize : use this flag when initial values needs to be changed
    enableReinitialize: true,

    initialValues: {
      user_id: "",
      title: "",
      amount: 0,
    },

    validationSchema: Yup.object().shape({
      title: Yup.string().required("    ."),
      amount: Yup.number()
        .min(1, "0    .")
        .required(" /    ."),
    }),
    onSubmit: async (values: any, { resetForm }) => {
      values.user_id = userId;
      if (!switch1) {
        values.amount = -values.amount;
      }
      await setUsdpInfo(values as SetUsdpParam);
      resetForm();
      tog_set_balance();
      dispatch(getUsers());
    },
  });

  document.title = "Dashboards | Users";
  return (
    <React.Fragment>
      <div className="page-content">
        <Container fluid>
          <Breadcrumb title="" breadcrumbItem="Users" />
          <TableContainer
            columns={columns}
            data={dashboardUsers || []}
            isGlobalFilter={true}
            isPagination={true}
            SearchPlaceholder={`${
              dashboardUsers ? dashboardUsers.length : 0
            } records...`}
            pagination="pagination"
            paginationWrapper="dataTables_paginate paging_simple_numbers"
            tableClass="dt-responsive nowrap w-100 dataTable no-footer dtr-inline table-hover"
            theadClass="table-light"
            isCustomPageSize={true}
            defaultSortingID="last_access"
          />
        </Container>

        <Modal
          isOpen={modal_set_balance}
          toggle={() => {
            tog_set_balance();
          }}
          centered
        >
          <ModalHeader
            toggle={() => {
              tog_set_balance();
            }}
          >
            <div className="modal-title mt-0 h5" id="myModalLabel">
               USDP .
            </div>
          </ModalHeader>
          <div className="modal-body">
            <Form onSubmit={validation.handleSubmit}>
              <Row className="mb-4">
                <Label
                  htmlFor="input_title"
                  className="col-sm-2 col-form-label"
                >
                  Title
                </Label>
                <Col sm={10}>
                  <Input
                    type="text"
                    name="title"
                    autoComplete="off"
                    placeholder=" ."
                    value={validation.values.title || ""}
                    onChange={validation.handleChange}
                    onBlur={validation.handleBlur}
                    invalid={
                      validation.touched.title && validation.errors.title
                        ? true
                        : false
                    }
                  />
                </Col>
                {validation.touched.title && validation.errors.title ? (
                  <FormFeedback type="invalid">
                    {validation.errors.title}
                  </FormFeedback>
                ) : null}
              </Row>
              <Row className="mb-3">
                <Switch
                  uncheckedIcon={<NegSymbol />}
                  checkedIcon={<PosSymbol />}
                  className="col-sm-2"
                  onColor="#38a4f8"
                  offColor="#ec4561"
                  onChange={() => setswitch1(!switch1)}
                  checked={switch1}
                />
                <Col sm={10}>
                  <Input
                    type="number"
                    name="amount"
                    autoComplete="off"
                    placeholder=" ."
                    value={validation.values.amount || ""}
                    onChange={validation.handleChange}
                    onBlur={validation.handleBlur}
                    invalid={
                      validation.touched.amount && validation.errors.amount
                        ? true
                        : false
                    }
                  />
                </Col>
              </Row>
              {validation.touched.amount && validation.errors.amount ? (
                <FormFeedback type="invalid">
                  {validation.errors.amount}
                </FormFeedback>
              ) : null}
              <div className="modal-footer">
                <Button
                  type="reset"
                  color="secondary"
                  onClick={() => tog_set_balance()}
                >
                  
                </Button>
                <Button type="submit" color="primary">
                  
                </Button>
              </div>
            </Form>
          </div>
        </Modal>
      </div>
    </React.Fragment>
  );
};

export default Dashboard;
